import * as React from 'react';
import { Route, RouteComponentProps, Switch, useLocation } from 'react-router-dom';
import { Sets } from '@app/Sets/Sets';
import { FlashCards } from '@app/FlashCards/FlashCards';
import { GeneralSettings } from '@app/Settings/General/GeneralSettings';
import { ProfileSettings } from '@app/Settings/Profile/ProfileSettings';
import { NotFound } from '@app/NotFound/NotFound';
import { useDocumentTitle } from '@app/utils/useDocumentTitle';
import { QuestionTable } from './Table/Table';
import { CoachHelper } from './CoachHelper/CoachHelper';

let routeFocusTimer: number;
export interface IAppRoute {
  label?: string; // Excluding the label will exclude the route from the nav sidebar in AppLayout
  /* eslint-disable @typescript-eslint/no-explicit-any */
  component: React.ComponentType<RouteComponentProps<any>> | React.ComponentType<any>;
  /* eslint-enable @typescript-eslint/no-explicit-any */
  exact?: boolean;
  path: string;
  title: string;
  routes?: undefined;
}

export interface IAppRouteGroup {
  label: string;
  routes: IAppRoute[];
}

export type AppRouteConfig = IAppRoute | IAppRouteGroup;

const routes: AppRouteConfig[] = [
  {
    component: Sets,
    exact: true,
    label: 'By Set',
    path: '/',
    title: 'JBQ | By Set',
  },
  {
    component: FlashCards,
    exact: true,
    label: 'FlashCards',
    path: '/flashcards',
    title: 'JBQ | FlashCards',
  },
  {
    component: QuestionTable,
    exact: true,
    label: 'Questions',
    path: '/questions',
    title: 'JBQ | Questions',
  },
  {
    component: CoachHelper,
    exact: true,
    label: 'CoachHelper',
    path: '/coach-helper',
    title: 'JBQ | Coach',
  }
  // {
  //   label: 'Settings',
  //   routes: [
  //     {
  //       component: GeneralSettings,
  //       exact: true,
  //       label: 'General',
  //       path: '/settings/general',
  //       title: 'PatternFly Seed | General Settings',
  //     },
  //     {
  //       component: ProfileSettings,
  //       exact: true,
  //       label: 'Profile',
  //       path: '/settings/profile',
  //       title: 'PatternFly Seed | Profile Settings',
  //     },
  //   ],
  // },
];

// a custom hook for sending focus to the primary content container
// after a view has loaded so that subsequent press of tab key
// sends focus directly to relevant content
// may not be necessary if https://github.com/ReactTraining/react-router/issues/5210 is resolved
const useA11yRouteChange = () => {
  const { pathname } = useLocation();
  React.useEffect(() => {
    routeFocusTimer = window.setTimeout(() => {
      const mainContainer = document.getElementById('primary-app-container');
      if (mainContainer) {
        mainContainer.focus();
      }
    }, 50);
    return () => {
      window.clearTimeout(routeFocusTimer);
    };
  }, [pathname]);
};

const RouteWithTitleUpdates = ({ component: Component, title, ...rest }: IAppRoute) => {
  useA11yRouteChange();
  useDocumentTitle(title);

  function routeWithTitle(routeProps: RouteComponentProps) {
    return <Component {...rest} {...routeProps} />;
  }

  return <Route render={routeWithTitle} {...rest} />;
};

const PageNotFound = ({ title }: { title: string }) => {
  useDocumentTitle(title);
  return <Route component={NotFound} />;
};

const flattenedRoutes: IAppRoute[] = routes.reduce(
  (flattened, route) => [...flattened, ...(route.routes ? route.routes : [route])],
  [] as IAppRoute[]
);

const AppRoutes = (): React.ReactElement => (
  <Switch>
    {flattenedRoutes.map(({ path, exact, component, title }, idx) => (
      <RouteWithTitleUpdates path={path} exact={exact} component={component} key={idx} title={title} />
    ))}
    <PageNotFound title="404 Page Not Found" />
  </Switch>
);

export { AppRoutes, routes };
